<template>
    <el-dialog title="标记更新列表信息"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name)"
    >
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="创建时间" prop="createTime">
                <el-date-picker
                        v-model="searchFormData.createTime"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <div class="v-center">
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="onClose(module_name)"><i class="iconfont icon-guanbi"></i> 关闭</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(360)">
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <!--<el-table-column-->
            <!--:show-overflow-tooltip="true"-->
            <!--prop="belong_to_server_id"-->
            <!--label="所属服务器id"-->
            <!--width="280">-->
            <!--</el-table-column>-->
            <el-table-column
                    prop="createTime"
                    label="创建时间"
                    width="160">
            </el-table-column>
            <el-table-column
                    width="156"
                    :show-overflow-tooltip="true"
                    prop="project_id"
                    label="项目名称"
                    :formatter="format_project">
            </el-table-column>
            <!--<el-table-column-->
            <!--prop="service_container_port"-->
            <!--label="容器端口"-->
            <!--width="80">-->
            <!--</el-table-column>-->
            <!--<el-table-column-->
            <!--:formatter="format_service_container_type"-->
            <!--prop="service_container_type_code_id"-->
            <!--label="容器类型">-->
            <!--</el-table-column>-->
            <el-table-column fixed="right" label="操作" width="120">
                <template slot-scope="scope">
                    <el-col :span="24">
                        <el-col :span="11">
                            <el-button type="danger" size="mini">容器已被关联</el-button>
                        </el-col>
                    </el-col>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="searchFormData.pageNum"
                :page-size="searchFormData.pageSize"
                :total="searchFormData.pageCount"
                :page-sizes="[11, 20, 50, 100]"
                layout="total, sizes, prev, pager, next, jumper"
        >
        </el-pagination>
    </el-dialog>
</template>
<script>
    export default {
        name: 'projectRollBackInfo',
        data() {
            return {
                module_name: 'projectRollBackInfo',
                project_code_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 11,
                    pageCount: 1,
                    order: '_createTime'
                },
                tableData: []
            }
        },
        async mounted() {
            this.project_code_list = await this.$getProjectCodeInfo();
            this.search();
        },
        computed: {
            pageInfo() {
                return this.$setPageInfo(this.module_name);
            }
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            async search() {
                let searchData = this.searchFormData;
                let data = await this.$search(searchData, 'OAMP_search_project_opt_record_info_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data;
            },
            format_project(row, column, cellValue) //格式化项目名称
            {
                return this.$formatCodeValue(row, this.project_code_list, column.property, '_id', 'project_name')
            },
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-input {
        width: 260px;
    }

    .el-select {
        width: 260px;
    }

    .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 260px;
    }
</style>


