<template>
    <div style="padding-right: 10px">
        <el-row :gutter="10">
            <el-col :span="5">
                <div class="cont" :style="'height:'+getAutoHeight(144)+';'">
                    <el-table :border="true" :stripe="true" size="mini" :height="getTableHeight(70)" ref="libJarTableRef" :data="libJarTableData"
                              @selection-change="ibResChange"
                              @cell-click="libResClick"
                    >
                        <el-table-column label="选择服务端资源">
                            <el-table-column
                                    type="selection"
                                    width="55">
                            </el-table-column>
                            <el-table-column
                                    prop="res_name"
                                    label="资源名称"
                                    show-overflow-tooltip
                            >
                            </el-table-column>
                            <!--<el-table-column-->
                            <!--prop="res_path"-->
                            <!--label="资源路径"-->
                            <!--show-overflow-tooltip>-->
                            <!--</el-table-column>-->
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
            <el-col :span="5">
                <div class="cont" :style="'height:'+getAutoHeight(144)+';'">
                    <el-table :border="true" :stripe="true" size="mini" :height="getTableHeight(330)" ref="ResourceLibTableRef" :data="ResourceLibTableData"
                              @selection-change="resourceLibResChange"
                              @cell-click="resourceLibResClick">
                        <el-table-column label="选择前端资源">
                            <el-table-column
                                    type="selection"
                                    width="55">
                            </el-table-column>
                            <el-table-column
                                    prop="res_name"
                                    label="资源名称"
                                    show-overflow-tooltip
                            >
                            </el-table-column>
                            <!--<el-table-column-->
                            <!--prop="res_path"-->
                            <!--label="资源路径"-->
                            <!--show-overflow-tooltip>-->
                            <!--</el-table-column>-->
                        </el-table-column>
                    </el-table>
                    <hr>
                    <el-table :border="true" :stripe="true" size="mini" :height="getTableHeight(620)" ref="staticFileDirPathTableRef" :data="staticFileDirPathTableData"
                              @selection-change="staticFileDirResChange"
                              @cell-click="staticFileDirResClick">
                        <el-table-column label="选择静态资源">
                            <el-table-column
                                    type="selection"
                                    width="55">
                            </el-table-column>
                            <el-table-column
                                    prop="res_name"
                                    label="资源名称"
                                    show-overflow-tooltip
                            >
                            </el-table-column>
                            <!--<el-table-column-->
                            <!--prop="res_path"-->
                            <!--label="资源路径"-->
                            <!--show-overflow-tooltip>-->
                            <!--</el-table-column>-->
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
            <el-col :span="14">
                <div class="cont" :style="'height:'+getAutoHeight(144)+';'">
                    <el-table :border="true" :stripe="true" size="mini" :height="getTableHeight(500)" :data="updateResTableData">
                        <el-table-column label="已选择更新内容">
                            <el-table-column fixed type="index" :index="onIndexMethod()" label="序号" width="60"></el-table-column>
                            <el-table-column
                                    prop="res_name"
                                    label="资源名称"
                                    show-overflow-tooltip
                                    width="260"
                            >
                            </el-table-column>
                            <el-table-column
                                    prop="resource_type_code_id"
                                    :formatter="format_resource_type"
                                    label="资源类型"
                                    width="80">
                            </el-table-column>
                            <el-table-column
                                    prop="res_path"
                                    label="资源路径"
                                    show-overflow-tooltip>
                            </el-table-column>
                        </el-table-column>
                    </el-table>
                    <hr>
                    <el-row>
                        <el-radio-group v-model="project_opt_type_code_id" :fill="'#e6a23c'" @change="project_opt_type_change">
                            <el-radio-button label="0">修改全量资源</el-radio-button>
                            <el-radio-button label="1">重启项目</el-radio-button>
                            <el-radio-button label="2">标记项目</el-radio-button>
                            <el-radio-button label="3">资源替换</el-radio-button>
                            <el-radio-button label="4">标记更新</el-radio-button>
                            <el-radio-button label="5">全量更新</el-radio-button>
                            <el-radio-button label="6">增量更新</el-radio-button>
                            <el-radio-button disabled label="7">回滚项目</el-radio-button>
                        </el-radio-group>
                    </el-row>
                    <hr>
                    <div class="form_update">
                        <el-form label-width="110px" ref="project_opt_form" :model="update_info" :rules="updateRules">
                            <el-row v-if="project_opt_type_code_id==='1'">
                                <el-form-item label="当前版本号:" prop="project_opt_version">
                                    <el-input disabled v-model="update_info.project_opt_version"></el-input>
                                </el-form-item>
                                <el-form-item label="重启原因:" prop="project_opt_summary">
                                    <el-input type="textarea" v-model="update_info.project_opt_summary"></el-input>
                                </el-form-item>
                            </el-row>
                            <el-row v-if="project_opt_type_code_id==='2'">
                                <el-form-item label="更新版号:" prop="project_opt_version">
                                    <el-input type="number" step="0.001" placeholder="请输入更新版号" v-model="update_info.project_opt_version"></el-input>
                                </el-form-item>
                                <el-form-item label="上个版号:" prop="project_opt_last_version">
                                    <el-input disabled v-model="update_info.project_opt_last_version"></el-input>
                                </el-form-item>
                                <el-form-item label="更新内容:" prop="project_opt_summary">
                                    <el-input type="textarea" placeholder="请输入更新内容" v-model="update_info.project_opt_summary"></el-input>
                                </el-form-item>
                            </el-row>
                            <el-row v-if="project_opt_type_code_id==='3'">
                                <el-form-item label="更新版号:" prop="project_opt_version">
                                    <el-input disabled v-model="update_info.project_opt_version"></el-input>
                                </el-form-item>
                                <el-form-item label="上个版号:" prop="project_opt_last_version">
                                    <el-input disabled v-model="update_info.project_opt_last_version"></el-input>
                                </el-form-item>
                                <el-form-item label="更新内容:" prop="project_opt_summary">
                                    <el-input type="textarea" placeholder="请输入更新内容" v-model="update_info.project_opt_summary"></el-input>
                                </el-form-item>
                            </el-row>
                            <el-row v-if="project_opt_type_code_id==='5'">
                                <el-form-item label="更新版号:" prop="project_opt_version">
                                    <el-input type="number" step="0.001" placeholder="请输入更新版号" v-model="update_info.project_opt_version"></el-input>
                                </el-form-item>
                                <el-form-item label="上个版号:" prop="project_opt_last_version">
                                    <el-input disabled v-model="update_info.project_opt_last_version"></el-input>
                                </el-form-item>
                                <el-form-item label="更新内容:" prop="project_opt_summary">
                                    <el-input type="textarea" placeholder="请输入更新内容" v-model="update_info.project_opt_summary"></el-input>
                                </el-form-item>
                            </el-row>
                            <el-row v-if="project_opt_type_code_id==='6'">
                                <el-form-item label="更新版号:" prop="project_opt_version">
                                    <el-input disabled v-model="update_info.project_opt_version"></el-input>
                                </el-form-item>
                                <el-form-item label="上个版号:" prop="project_opt_last_version">
                                    <el-input disabled v-model="update_info.project_opt_last_version"></el-input>
                                </el-form-item>
                                <el-form-item label="更新内容:" prop="project_opt_summary">
                                    <el-input type="textarea" placeholder="请输入更新内容" v-model="update_info.project_opt_summary"></el-input>
                                </el-form-item>
                            </el-row>
                        </el-form>
                    </div>
                    <hr>
                    <el-row class="v-center">
                        <el-button v-if="(project_info !== null) && project_opt_type_code_id !== '-1'  && project_opt_type_code_id !== '4' " type="warning" :loading="is_exec_loading" @click="project_opt_exec()">执行</el-button>
                        <el-button v-else disabled type="warning">执行</el-button>
                    </el-row>
                </div>
            </el-col>
        </el-row>
        <projectRemarkUpdateInfo></projectRemarkUpdateInfo>
        <projectRollBackInfo></projectRollBackInfo>
    </div>
</template>

<script>
    import projectRemarkUpdateInfo from './projectRemarkUpdateInfo'
    import projectRollBackInfo from './projectRollBackInfo'
    export default {
        name: 'projectUpdateInfo',
        components: {projectRemarkUpdateInfo,projectRollBackInfo},
        data() {
            return {
                libJarTableData: [],
                ResourceLibTableData: [],
                staticFileDirPathTableData: [],
                updateResTableData: [],
                selectedLibRes: [],
                selectedResourceLibRes: [],
                selectedStaticFileDirRes: [],
                project_opt_type_code_id: '-1',
                update_info: {
                    project_opt_version: '',
                    project_opt_summary: '',
                    project_opt_last_version: '',
                },
                cur_project_id: '',
                resource_type_code_list: null,
                local_update_res_path: '',
                updateRules: {
                    project_opt_version: [{required: true, message: '请输入版本号', trigger: 'blur'}],
                },
                is_exec_loading: false,
                project_info: null,
                update_conf_info: null,
            }
        },
        async mounted() {
            this.cur_project_id = this.$store.getters.get_authority_project_id;
            if (this.cur_project_id != null && this.cur_project_id !== '') {
                this.resource_type_code_list = await this.$setCodeList(this.resource_type_code_list, 'resource_type');
                let {data} = await this.$search({project_id: this.cur_project_id}, "OAMP_search_project_update_conf_info_list");
                if (data != null) {
                    let update_conf_info = data[0];
                    this.update_conf_info = update_conf_info
                    this.local_update_res_path = update_conf_info.update_path;
                    await this.initPage()
                } else {
                    this.$message.warning('未配置项目更新信息')
                }
            }
            else {
                this.$message.error('页面错误，未选择当前项目');
                // this.$router.push({name: 'home'});
            }
        },
        methods: {
            async initPage() {
                let item1 = {
                    path: this.local_update_res_path,
                    isShowFile: '1'
                };
                let x = await this.$exec(item1, 'OAMP_get_lib_res_info');
                if (x != null) {
                    this.libJarTableData = x.data;
                }
                let item2 = {
                    path: this.local_update_res_path,
                    isShowFile: '1'
                };
                let y = await this.$exec(item2, 'OAMP_get_ResourceLib_res_info');
                if (y != null) {
                    this.ResourceLibTableData = y.data;
                }
                let item3 = {
                    path: this.local_update_res_path,
                    isShowFile: '0'
                };
                let z = await this.$exec(item3, 'OAMP_get_static_res_info');
                if (z != null) {
                    this.staticFileDirPathTableData = z.data;
                }
                this.search_project_info()

            },
            async search_project_info() {
                if (this.cur_project_id != null && this.cur_project_id !== '') {
                    /*获取项目之前信息*/
                    let item4 = {project_id: this.cur_project_id};
                    let {data} = await this.$search(item4, 'OAMP_search_project_info_by_id');
                    this.project_info = data;
                }
            },
            ibResChange(val) /*选择行*/ {
                this.selectedLibRes = val;
                this.updateResTableData = [].concat(this.selectedLibRes).concat(this.selectedResourceLibRes).concat(this.selectedStaticFileDirRes);
            },
            resourceLibResChange(val) /* 选择行*/ {
                this.selectedResourceLibRes = val;
                this.updateResTableData = [].concat(this.selectedLibRes).concat(this.selectedResourceLibRes).concat(this.selectedStaticFileDirRes);
            },
            staticFileDirResChange(val) /* 选择行*/ {
                this.selectedStaticFileDirRes = val;
                this.updateResTableData = [].concat(this.selectedLibRes).concat(this.selectedResourceLibRes).concat(this.selectedStaticFileDirRes);
            },
            libResClick(row, column, cell, event)/*lib行点击事件*/ {
                this.$refs.libJarTableRef.toggleRowSelection(row);
            },
            resourceLibResClick(row, column, cell, event)/*lib行点击事件*/ {
                this.$refs.ResourceLibTableRef.toggleRowSelection(row);
            },
            staticFileDirResClick(row, column, cell, event)/*lib行点击事件*/ {
                this.$refs.staticFileDirPathTableRef.toggleRowSelection(row);
            },
            format_resource_type(row, column, cellValue)/*格式化资源类型*/ {
                return this.$formatCodeValue(row, this.resource_type_code_list, column.property)
            },
            async project_modify_full_res()/*修改全量资源*/ {
                this.update_conf_info['update_ex_resource'] = [{'libRes': this.selectedLibRes}, {'ResourceLibRes': this.selectedResourceLibRes}, {'staticFileRes': this.selectedStaticFileDirRes}];
                let data = await this.$update(this.update_conf_info, 'OAMP_update_project_update_conf_info');
                this.is_exec_loading = false;
                if (data != null) {
                    this.$message.success('修改全量资源信息成功');
                } else {
                    this.$message.error('修改全量资源信息失败');
                }
            },
            async project_restart()/*重启项目*/ {
                let item = {
                    project_id: this.cur_project_id,
                    project_opt_summary: this.update_info.project_opt_summary,
                    project_opt_version: this.update_info.project_opt_version,
                };
                let data = await this.$exec(item, 'OMAP_project_restart');
                this.is_exec_loading = false;
                if (data != null && data.is_success === '1') {
                    this.$message.success('项目重启操作成功');
                    // let msg = this.project_opt_notify_info('项目重启', data.data, true);
                    // this.$notify({title: '项目重启操作信息', dangerouslyUseHTMLString: true, message: msg, offset: 300});
                } else if (data.is_success === '0') {
                    this.$message.warning('项目重启操作异常');
                    // let msg = this.project_opt_notify_info('项目重启', data.data, false);
                    // this.$notify({title: '项目重启操作信息', dangerouslyUseHTMLString: true, message: msg, offset: 300});
                }
            },
            async project_remark_version()/*标记项目*/ {
                /*OAMP_project_remark_version*/
                let item = {
                    project_id: this.cur_project_id,
                    libRes: this.selectedLibRes,
                    resourceLibRes: this.selectedResourceLibRes,
                    staticFileRes: this.selectedStaticFileDirRes,
                    project_opt_version: this.update_info.project_opt_version,
                    project_opt_summary: this.update_info.project_opt_summary,
                    project_opt_last_version: this.update_info.project_opt_last_version,
                };
                let data = await this.$exec(item, 'OAMP_project_remark_version');
                this.is_exec_loading = false;
                if (data != null && data.is_success === '1') {
                    this.$message.success('标记项目成功');
                    await this.search_project_info();
                    this.project_opt_type_change(this.project_opt_type_code_id);
                } else {
                    this.$message.warning('项目标记异常，请重试或者联系运维人员')
                }
            },
            async project_static_file_update()/*替换资源*/ {
                /*OAMP_project_static_file_update*/
                let item = {
                    project_id: this.cur_project_id,
                    libRes: this.selectedLibRes,
                    resourceLibRes: this.selectedResourceLibRes,
                    staticFileRes: this.selectedStaticFileDirRes,
                    project_opt_version: this.update_info.project_opt_version,
                    project_opt_summary: this.update_info.project_opt_summary,
                    project_opt_last_version: this.update_info.project_opt_last_version,
                };
                let data = await this.$exec(item, 'OAMP_project_static_file_update');
                this.is_exec_loading = false;
                if (data != null && data.is_success === '1') {
                    this.$message.success('项目资源替换操作成功');
                    // let msg = this.project_opt_notify_info('项目资源替换', data.data, true);
                    // this.$notify({title: '项目重启操作信息', dangerouslyUseHTMLString: true, message: msg, offset: 300});
                } else if (data.is_success === '0') {
                    this.$message.warning('项目资源替换操作异常');
                    // let msg = this.project_opt_notify_info('项目资源替换', data.data, false);
                    // this.$notify({title: '项目资源替换操作信息', dangerouslyUseHTMLString: true, message: msg, offset: 300});
                }
            },
            async project_full_update()/*全量更新*/ {
                /*OAMP_project_full_update*/
                let item = {
                    project_id: this.cur_project_id,
                    libRes: this.selectedLibRes,
                    resourceLibRes: this.selectedResourceLibRes,
                    staticFileRes: this.selectedStaticFileDirRes,
                    project_opt_version: this.update_info.project_opt_version,
                    project_opt_summary: this.update_info.project_opt_summary,
                    project_opt_last_version: this.update_info.project_opt_last_version,
                };
                let data = await this.$exec(item, 'OAMP_project_full_update');
                this.is_exec_loading = false;
                if (data != null && data.is_success === '1') {
                    this.$message.success('项目全量更新操作成功');
                    // let msg = this.project_opt_notify_info('项目全量更新', data.data, true);
                    // this.$notify({title: '项目全量更新操作信息', dangerouslyUseHTMLString: true, message: msg, offset: 300});
                } else if (data.is_success === '0') {
                    this.$message.warning('项目全量更新操作异常');
                    // let msg = this.project_opt_notify_info('项目全量更新', data.data, false);
                    // this.$notify({title: '项目全量更新操作信息', dangerouslyUseHTMLString: true, message: msg, offset: 300});
                }
            },
            async project_increase_update()/*增量更新*/ {
                /*OAMP_project_increase_update*/
                let item = {
                    project_id: this.cur_project_id,
                    libRes: this.selectedLibRes,
                    resourceLibRes: this.selectedResourceLibRes,
                    staticFileRes: this.selectedStaticFileDirRes,
                    project_opt_version: this.update_info.project_opt_version,
                    project_opt_summary: this.update_info.project_opt_summary,
                    project_opt_last_version: this.update_info.project_opt_last_version,
                };
                let data = await this.$exec(item, 'OAMP_project_increase_update');
                this.is_exec_loading = false;
                if (data != null && data.is_success === '1') {
                    this.$message.success('项目增量更新操作成功');
                    // let msg = this.project_opt_notify_info('项目增量更新', data.data, true);
                    // this.$notify({title: '项目增量更新操作信息', dangerouslyUseHTMLString: true, message: msg, offset: 300});
                } else if (data.is_success === '0') {
                    this.$message.warning('项目增量更新操作异常');
                    // let msg = this.project_opt_notify_info('项目增量更新', data.data, false);
                    // this.$notify({title: '项目增量更新操作信息', dangerouslyUseHTMLString: true, message: msg, offset: 300});
                }
            },
            project_opt_notify_info(header, res_data, is_success) {
                if (is_success) {
                    let msg = '<i>' + header + '成功服务器共计<strong><i>' + res_data.success_count + '</i></strong>个<br>成功ip为:<br>';
                    res_data.success_ips.forEach((data) => {
                        msg += '[<strong><i>' + data + '</i></strong>]<br>'
                    });
                    msg += '</i>';
                    return msg
                } else {
                    let msg = '<strong><i>' + header + '服务器总数' + res_data.exec_count + '个</i></strong><br>';
                    msg += '<i>' + header + '成功服务器共计<strong><i>' + res_data.success_count + '</i></strong>个<br>成功ip为:<br>';
                    res_data.success_ips.forEach((data) => {
                        msg += '[<strong><i>' + data + '</i></strong>]<br>'
                    });
                    msg += '</i>';
                    msg += '<i>' + header + '失败服务器共计<strong><i>' + res_data.failed_count + '</i></strong>个<br>失败ip为:<br>';
                    res_data.failed_ips.forEach((data) => {
                        msg += '[<strong><i>' + data + '</i></strong>]<br>'
                    });
                    msg += '</i>';
                    return msg
                }
            },
            add_version(version) {
                let version_arr = version.toString().split('\.');
                if (version_arr.length <= 1)
                    return (parseInt(version_arr[0])).toString() + '.001';
                else {
                    let str1 = '000'.concat((parseInt(version_arr[1]) + 1).toString());
                    return (version_arr[0]) + '.' + str1.substring(str1.length - 3, str1.length).toString()
                }
            },
            project_opt_type_change(val)/*项目操作类型切换*/ {
                this.update_info.project_opt_summary = '';
                switch (parseInt(val)) {
                    case 1:
                        this.update_info.project_opt_version = this.project_info.project_version;
                        break;
                    case 2:
                        this.update_info.project_opt_version = this.add_version(this.project_info.project_version);
                        this.update_info.project_opt_last_version = this.project_info.project_version;
                        break;
                    case 3:
                        this.update_info.project_opt_version = this.add_version(this.project_info.project_version);
                        this.update_info.project_opt_last_version = this.project_info.project_version;
                        break;
                    case 4: /*标记更新*/
                        this.onAdd('projectRemarkUpdateInfo');
                        break;
                    case 5:
                        this.update_info.project_opt_version = parseInt(this.project_info.project_version) + 1;
                        this.update_info.project_opt_last_version = this.project_info.project_version;
                        break;
                    case 6:
                        this.update_info.project_opt_version = this.add_version(this.project_info.project_version);
                        this.update_info.project_opt_last_version = this.project_info.project_version;
                        break;
                    case 7:
                        this.onAdd('projectRollBackInfo');
                        break;
                }
            },
            project_opt_exec()/*执行 切换功能选项*/ {
                this.$refs['project_opt_form'].validate((valid) => {
                    if (valid) {
                        this.is_exec_loading = true;
                        switch (parseInt(this.project_opt_type_code_id)) {
                            case 0:
                                this.project_modify_full_res();
                                break;
                            case 1:
                                this.project_restart();
                                break;
                            case 2:
                                this.project_remark_version();
                                break;
                            case 3:
                                this.project_static_file_update();
                                break;
                            case 4:
                                break;
                            case 5:
                                this.project_full_update();
                                break;
                            case 6:
                                this.project_increase_update();
                                break;
                            case 7:
                                break;
                        }
                    }
                });
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .cont {
        background-color: #FFF;
        border-radius: 15px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
        padding: 15px;
    }

    .form_update {
        height: 300px;
    }
</style>


