<template>
    <el-dialog title="标记更新列表信息"
               width="1200px"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name)"
    >
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="创建时间" prop="createTime">
                <el-date-picker
                        v-model="searchFormData.createTime"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <div class="v-center">
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="onClose(module_name)"><i class="iconfont icon-guanbi"></i> 关闭</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(360)">
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column
                    prop="createTime"
                    label="创建时间"
                    width="160">
            </el-table-column>
            <el-table-column
                    width="156"
                    :show-overflow-tooltip="true"
                    prop="project_id"
                    label="项目名称"
                    :formatter="format_project">
            </el-table-column>
            <el-table-column
                    prop="project_opt_type_code_id"
                    label="操作类型"
                    width="80"
                    :formatter="format_project_opt_type"
            >
            </el-table-column>
            <el-table-column
                    prop="project_opt_version"
                    label="操作版本"
                    width="80"
            >
            </el-table-column>
            <el-table-column
                    prop="is_opt_success"
                    label="操作结果"
                    width="80"
                    :formatter="format_is_opt_success"
            ></el-table-column>
            <el-table-column
                    prop="project_opt_last_version"
                    label="上个版本"
                    width="80"
            >
            </el-table-column>
            <el-table-column
                    prop="exec_state_code_id"
                    label="远程执行状态"
                    :formatter="format_exec_state"
                    width="100"
            >
            </el-table-column>
            <el-table-column
                    prop="project_opt_summary"
                    label="操作描述"
                    :show-overflow-tooltip="true"
            >
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="200">
                <template slot-scope="scope">
                    <el-col v-if="scope.row.is_opt_success ==='1'" :span="12">
                        <el-button type="primary" size="mini" @click="remark_update(scope.$index, tableData)">标记更新</el-button>
                    </el-col>
                    <el-col v-if="scope.row.is_opt_success ==='1'" :span="12">
                        <el-button type="primary" size="mini" @click="ramark_cancel(scope.$index, tableData)">标记取消</el-button>
                    </el-col>
                </template>
            </el-table-column>
        </el-table>
        <el-paginations
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="searchFormData.pageNum"
                :page-size="searchFormData.pageSize"
                :total="searchFormData.pageCount"
                :page-sizes="[11, 20, 50, 100]"
                layout="total, sizes, prev, pager, next, jumper"
        >
        </el-paginations>
    </el-dialog>
</template>
<script>
    export default {
        name: 'projectRemarkUpdateInfo',
        data() {
            return {
                module_name: 'projectRemarkUpdateInfo',
                project_code_list: null,
                project_opt_type_code_list: null,
                exec_state_code_list: null,
                searchFormData: {
                    project_id: this.$store.getters.get_authority_project_id,
                    project_opt_type_code_id: '2', /*项目操作类型  标记项目*/
                    pageNum: 1,
                    pageSize: 11,
                    pageCount: 1,
                    order: '-createTime'
                },
                tableData: []
            }
        },
        async mounted() {
            this.project_code_list = await this.$getProjectCodeInfo();
            this.project_opt_type_code_list = await this.$setCodeList(this.project_opt_type_code_list, 'project_opt_type');
            this.exec_state_code_list = await this.$setCodeList(this.exec_state_code_list, 'exec_state');
            this.search();
        },
        computed: {
            pageInfo() {
                return this.$setPageInfo(this.module_name);
            }
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            async search() {
                let searchData = this.searchFormData;
                let data = await this.$search(searchData, 'OAMP_search_project_opt_record_info_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data;
            },
            format_project(row, column, cellValue) //格式化项目名称
            {
                return this.$formatCodeValue(row, this.project_code_list, column.property, '_id', 'project_name')
            },
            format_project_opt_type(row, column, cellValue) // 格式化项目操作类型
            {
                return this.$formatCodeValue(row, this.project_opt_type_code_list, 'project_opt_type_code_id')
            },
            format_is_opt_success(row, column, cellValue) {
                if (row.is_opt_success === '1') {
                    return '成功'
                } else if (row.is_opt_success === '0') {
                    return '失败'
                }
                else
                    return '暂无结果'
            },
            format_exec_state(row, column, cellValue) // 格式化项目操作类型
            {
                return this.$formatCodeValue(row, this.exec_state_code_list, 'exec_state_code_id')
            },
            remark_update(index, rows)  // 标记更新
            {
                let rowData = rows[index];
                let res = this.$exec(rowData,'OAMP_project_remark_update')
            },
            ramark_cancel(index, rows)  // 标记取消
            {
                let rowData = rows[index];
                let re = this.$exec(rowData,'OAMP_project_remark_cancel')
                if (re.is_success === '1')
                {
                    rows[index] = re.data;
                }else{
                    this.$message.error('项目标记取消失败!')
                }
            },
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-input {
        width: 260px;
    }

    .el-select {
        width: 260px;
    }

    .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 260px;
    }
</style>


